.app-mine {
  width: 80%;
  margin: 0 auto;
}
.app-mine-item {
  margin-bottom: 0.2rem;
}
.app-mine-title {
  font-weight: bold;
  display: flex;
  align-items: center;
}
.app-mine .mine-item {
  width: 25%;
  display: inline-block;
  padding: 0.2rem;
}
.app-mine .mine-item img {
  height: 2rem;
}
.app-mine .mine-item .desc {
  text-align: center;
  margin-top: 0.1rem;
}
.app-mine .plan-item {
  margin: 0.1rem 1em;
  display: flex;
  align-items: center;
}
.app-mine .plan-item:before {
  content: '';
  display: inline-block;
  border-radius: 50%;
  background-color: #040809;
  margin: 0 0.2rem;
  padding: 0.04rem;
}
